.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
  height: 60px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: #404040;
}

.App-title {
  font-size: 6em;
  font-weight: 300;
  margin: 0;
}

.App-main {
  width: 100%;
  height: 100%;
}
.App-search_bar_container {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16),0 0 0 1px rgba(0,0,0,0.08);
          box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16),0 0 0 1px rgba(0,0,0,0.08);
  -webkit-transition: -webkit-box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1);
  transition: -webkit-box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1);
  -o-transition: box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1);
  transition: box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1);
  transition: box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1), -webkit-box-shadow 200ms cubic-bezier(0.4, 0.0, 0.2, 1);
}
.App-search_bar {
  width: 90%;
}
.App-search_bar input[type=search]{
  position: relative;
  width: 100%;
  min-width: calc(100% - 35%);
  height: 44px;
  border: none;
  border-radius: 2px 0 0 2px;
  padding: 8px;
  padding-left: 16px;
  font-size: 0.855555rem;
}
.App-search_bar_icon {
  width: 10%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background: #fff;
  border-radius: 0px 2px 2px 0px;
}
.App-button {
  height: 36px;
  line-height: 27px;
  background-image: -webkit-gradient(linear,left top,left bottom,from(#f5f5f5),to(#f1f1f1));
  border: 1px solid #f2f2f2;
  border-radius: 2px;
  color: #757575;
  cursor: default;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  margin: 11px 4px;
  min-width: 54px;
  padding: 0 16px;
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



